/* ALL App.js styling */

/* HEADER */
.header {
  text-align: center;
  background: white;
}
.logo {
  width: 100vw;
  top:0;
}

/* BODY */
.body{
  background: white;
  text-align: center;
  display: inline-block;
}
.button {
  border-radius: 4px;
  background-color: #98B0C3;
  border: none;
  color: #FFFFFF;
  padding: 1vh;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  width: 90vw;
    display: inline-block;
    height: 15vh;
    font-size: 10vw;
}
button:hover  {
  background-color:black;
}


/* CAROUSEL */
.carousel {
  width: 100vw;
}

/* TEXT STYLING */
.writeup-containter {
  text-align: center;
}

.subheading {
  font-size: 30px;
  margin-bottom: 0;
}

a:link {
  text-decoration: none;
  color: black;
}
a:visited {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: underline;
  color: black;
}
a:active {
  text-decoration: underline;
  color: black;
}

.description {
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 20px;
  color: #304453;

  height:auto;
}

/* COLLAPSIBLE */
.Collapsible__trigger {
  font-family: Helvetica;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  position: relative;
  color: black;
  background-color: #98B0C3;
  border: black solid 1vw;
  transition: all 0.5s;
  cursor: pointer;
  width: 90vw;
  display: inline-block;
  height: 15vh;
  font-size: 10vw;
  margin: 1vh;
  height:auto;
}
Collapsible__trigger:after {
    content: '\f107';
    position: absolute;
    right: 10px;
    top: 10px;
    display: block;
    transition: transform 300ms;
  }
Collapsible__trigger.is-open :after {
  background-color:#616b75;
}

/*biggest accordian custom styling*/

/* COLLAPSIBLE */
.Collapsible__trigger.mainCol {
  font-family: Helvetica;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  position: relative;
  color: #98B0C3;
  background-color: black;
  border: black solid 1vw;
  transition: all 0.5s;
  cursor: pointer;
  width: 100vw;
  display: inline-block;
  font-size: 8vw;
  height:10vw;
  margin: 0;
  margin-bottom: 1vh;
}
Collapsible__trigger.mainCol:after {
    content: '\f107';
    position: absolute;
    right: 10px;
    top: 10px;
    display: block;
    transition: transform 300ms;
  }

.mainCol {
  width: 100vw;
  background-color: black;
  margin: 0;
  color: #98B0C3;
  margin-bottom: 20px;
}
.mainHov:hover {
  background-color:black;
  color: #98B0C3;
  margin-bottom: 20px;
}

/* smaller custom styles */
.Collapsible__trigger.subCol {
  font-family: 'Segoe UI';
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  position: relative;
  color: black;
  background-color: #98B0C3;
  border: black solid 1vw;
  transition: all 0.5s;
  cursor: pointer;
  width: 92vw;
  display: inline-block;
  margin-left: 4vw;
  margin-right: 4vw;
  margin-bottom: 1vh;
  font-size: 4vw;
  height:8vw;
  vertical-align: middle;
  line-height: 2em;
}
Collapsible__trigger.subCol:after {
    content: '\f107';
    position: absolute;
    right: 10px;
    top: 10px;
    display: block;
    transition: transform 300ms;
  }
.subCol {
  background-color:#b3b9c0;
  font-size: 6vw;
  margin-left: 5vw;  
  margin-right: 5vw;  
  width: 90vw;
  height:auto;
}



.subCol { /*clicked and open */
